.Contact{
    min-height:50vh;
    text-align: center;
    padding-top:150px;
    background-color:white;
    color:#2b2b2b;
   
}
.contacts{
display:-ms-flexbox;
display:flex;
-ms-flex-pack:distribute;
    justify-content:space-around;
font-size:25px;

}
@media (max-width: 900px) {
    .contacts{
        -ms-flex-direction:column;
            flex-direction:column;
        -ms-flex-pack:center;
            justify-content:center;
        -ms-flex-align:center;
            align-items:center;
    }
}
.contacts h2{
    font-size:35px;
}
.contacts p{
    letter-spacing: 2px;
}


