
.BodyApp{
    
    background-color:white;
    min-height:100vh;
    text-align: center;
    padding-top:150px;
    
   
}

h1{
    font-family: 'Open Sans', sans-serif;
    color:#2b2b2b;
    font-size:70px;
    letter-spacing: 1px
}



@media (max-width: 900px) {
    h1{
        font-size:40px;
    }
  .imgrid{
      display:-ms-flexbox;
      display:flex;
      -ms-flex-direction:column;
          flex-direction:column;
      -ms-flex-align:center;
          align-items:center;
      
    }
    #test{
        width:80%;
    }
    
    

}


div.item {
  position: relative;
  float: left;
  overflow: hidden;
  width:30%;
  margin:1.66%;
  background: #000000;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  border-radius:15px;
}



div.item img {
  max-width: 100%;
  vertical-align: top;
  
}

div.item i {
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  font-size: 34px;
  color: #000000;
  width: 60px;
  height: 60px;
  line-height: 60px;
  background: #ffffff;
}

div.item a {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
}

div.item.blue {
  background-color: #34495e;
}
div.item.blue i {
  color: #34495e;
}

div.item:hover img {
  opacity: 0.3;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

div.item * {
  -webkit-transition: all .35s ease-in-out;
  -o-transition: all .35s ease-in-out;
  transition: all .35s ease-in-out;
}

div.item i {
  -webkit-transform: translate(-80%, -80%) scale(0);
      -ms-transform: translate(-80%, -80%) scale(0);
          transform: translate(-80%, -80%) scale(0);
  -webkit-transition: -webkit-transform 300ms 0ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
  transition: -webkit-transform 300ms 0ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
  -o-transition: transform 300ms 0ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
  transition: transform 300ms 0ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
  transition: transform 300ms 0ms cubic-bezier(0.6, -0.28, 0.735, 0.045), -webkit-transform 300ms 0ms cubic-bezier(0.6, -0.28, 0.735, 0.045);
}

div.item:hover i {
  -webkit-transform: translate(-80%, -80%) scale(1);
      -ms-transform: translate(-80%, -80%) scale(1);
          transform: translate(-80%, -80%) scale(1);
  -webkit-transition: -webkit-transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: -webkit-transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -o-transition: transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 300ms 100ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
 



