
body {

  margin: 0;
 	font-family: 'Quicksand', sans-serif;
	background-color:#2b2b2b ;

}
.name{
  display:-ms-flexbox;
  display:flex;
  font-weight:bolder;
  font-size:40px;
   color:white;
  -ms-flex-pack:start;
      justify-content:flex-start;
  
}
nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align:center;
      align-items:center;
  min-height:10vh;
  
}

a {
 	 padding:15px;
	 text-decoration:none;
   font-size:25px;
   color:white;
   margin:20px;
}

a:hover{
  color:#7ebcd8;
  -webkit-transition:0.2s ease-out;
  -o-transition:0.2s ease-out;
  transition:0.2s ease-out;
}
i{
  margin-left:20px;
}

@media  (max-width: 1250px) {
  nav {
    font-size:20px;
    -ms-flex-pack: end;
        justify-content: flex-end;
  }
 
}

@media  (max-width:1150px){
 .name{
   display:none;
 }
nav {
    -ms-flex-pack: center;
        justify-content: center;
  }

  a {
    
    font-size: 35px;
  }
}

@media (max-width: 1150px) {
  nav {
    
    -ms-flex-direction: column;
    
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
   
  }
  a {
     padding-right:600px;
    font-size:50px;
    
  }
 i {
   display:none;
}
}
@media (max-width: 950px){
  a{
    padding-right:300px;
  }
}
@media (max-width: 650px){
  a{
    padding-right:100px;
  }
}
@media(max-width:490px){
  a{
    padding-right:20px;
  }
}
@media(max-width:370px){
  a{
    padding-right:10px;
    font-size:40px;
  }
}



